.container {
    margin: 20px auto;
    padding: 20px;
    max-width: 900px;
    width: 100%;
}

.search-box {
    width: 100%;
    padding: 10px;
    margin-bottom: 20px;
    font-size: 1rem;
    border: 2px solid #ccc;
    border-radius: 5px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

.data-table {
    width: 100%;
    border-collapse: collapse;
}

.data-table th, .data-table td {
    padding: 10px;
    text-align: left;
    border-bottom: 1px solid #ddd;
}

.data-table th {
    background-color: #f4f4f4;
    font-weight: bold;
}

.data-table tr:hover {
    background-color: #f1f1f1;
}

.view-btn {
    background-color: #007bff;
    color: white;
    padding: 8px 16px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.view-btn:hover {
    background-color: #0056b3;
}

/* Responsive Design */
@media (max-width: 768px) {
    .data-table th, .data-table td {
        padding: 8px;
        font-size: 0.9rem;
    }

    .search-box {
        font-size: 0.9rem;
    }
}

@media (max-width: 480px) {
    .data-table th, .data-table td {
        padding: 6px;
        font-size: 0.8rem;
    }

    .search-box {
        font-size: 0.8rem;
    }
}
